.posts {
  transform: translateY(-80px); }

.post {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
  width: 80%;
  border-radius: 10px;
  margin: 30px auto;
  padding: 30px;
  background-color: #fff;
  display: block; }
  .post__title {
    font-size: 32px;
    margin: 10px 0px; }
  .post__body {
    margin: 20px 0px; }

.header {
  background-image: linear-gradient(to left top, #ffe943, #ffe13d, #ffd937, #ffd032, #ffc82d);
  padding-top: 145px;
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  top: 0; }
  .header__container {
    text-align: center; }
  .header__img {
    transform: rotate(90deg); }

.comments {
  width: 80%;
  margin: auto; }

.footer {
  padding: 30px 0px; }

.nav {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background-color: #011627;
  position: fixed;
  z-index: 1;
  width: 100%;
  top: 0;
  left: 0;
  transition: transform .4s ease; }
  .nav__container {
    list-style: none;
    display: flex;
    align-items: center;
    padding: 0;
    margin: 0; }
  .nav__element {
    padding: 60px 75px; }
  .nav__logo {
    padding: 60px 75px; }
  .nav__expand {
    padding: 60px 75px;
    margin-right: 0px;
    margin-left: auto;
    display: none; }
  .nav__link {
    color: #fff;
    text-decoration: none; }
  @media screen and (max-width: 1024px) {
    .nav__element {
      display: none; }
    .nav__expand {
      display: initial; } }

.dropdown {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background-color: #011627;
  z-index: 100;
  transition: transform .2s ease; }
  .dropdown--closed {
    transform: translateX(-100%); }
  .dropdown__close {
    position: absolute;
    right: 30px;
    top: 30px; }
  .dropdown__container {
    position: absolute;
    list-style: none;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0;
    padding: 0; }
  .dropdown__element {
    padding: 20px 0px; }
  .dropdown__link {
    color: #fff;
    text-decoration: none;
    font-size: 28px; }

body {
  margin: 0px;
  font-family: 'Roboto', sans-serif; }
